import FavoriteRestaurantIdb from "../data/favorite-restaurant-idb";
import { createRestoLikeButtonTemplate, createRestoLikedButtonTemplate } from "../views/templates/template-creator";

const LikeButtonInitiator = {
  async init({ likeButtonContainer, restaurant }) {
    this._likeButtonContainer = likeButtonContainer;
    this._restaurant = restaurant;

    await this._renderButton();
  },

  async _renderButton() {
    // Tambahkan pengecekan apakah this._restaurant telah terdefinisi
    if (!this._restaurant) {
      throw new Error("Restaurant is not defined");
    }

    const { id } = this._restaurant;

    if (await this._isRestaurantExist(id)) {
      this._renderLiked();
    } else {
      this._renderLike();
    }
  },

  async _isRestaurantExist(id) {
    const restaurant = await FavoriteRestaurantIdb.getRestaurant(id);
    return !!restaurant;
  },

  _renderLike() {
    this._likeButtonContainer.innerHTML = createRestoLikeButtonTemplate();

    const likeButton = document.querySelector("#likeButton");
    if (likeButton) {
      likeButton.addEventListener("click", async () => {
        await FavoriteRestaurantIdb.putRestaurant(this._restaurant);
        this._renderButton();
      });
    }
  },

  _renderLiked() {
    this._likeButtonContainer.innerHTML = createRestoLikedButtonTemplate();

    const likeButton = document.querySelector("#likeButton");
    if (likeButton) {
      likeButton.addEventListener("click", async () => {
        await FavoriteRestaurantIdb.deleteRestaurant(this._restaurant.id);
        this._renderButton();
      });
    }
  },
};

export default LikeButtonInitiator;
