  padding: 0;
  margin: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* AppBar */

.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
  min-width: 44px;
  min-height: 44px;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand h1 {
  color: #db0000;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 8px;
  margin-bottom: 5px;
  width: 44px;  /* Explicitly set width */
  height: 44px;  /* Explicitly set height */
  font-size: 20px;
}

.skip-link {
  position: absolute;
  top: -30px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 0;  /* Padding dihapus untuk mengakomodasi ukuran tetap */
  z-index: 100;
  width: 44px;  /* Ukuran lebar tetap 44px */
  height: 44px;  /* Ukuran tinggi tetap 44px */
  display: flex;  /* Menggunakan flexbox untuk penempatan konten */
  align-items: center;  /* Konten berada di tengah secara vertikal */
  justify-content: center;  /* Konten berada di tengah secara horizontal */
  border-radius: 4px;  /* Menambahkan border radius untuk gaya */
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);  /* Menambahkan shadow untuk visibilitas */
  text-align: center;
  font-size: 10px;
}

.skip-link:focus {
  top: 0;
}

main {
  padding: 32px;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;  /* Add this line */
}

.content {
  margin: 0 auto;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;  /* Add this line */
}

.content .content__heading {
  font-weight: normal;
  text-align: center;  /* Add this line if needed */
}

/* Home Page Specific */
.home-page-header {
  text-align: center;
  margin-bottom: 20px;
}

/* Restaurants */

.restaurants {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  margin: 32px 0;
}

/* Restaurant Item */

.restaurant-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}

.restaurant-item__header {
  position: relative;
}

.restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
}

.restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {
  margin-left: 10px;
}

.restaurant-item__content {
  padding: 16px;
}

.restaurant-item__content h3 {
  margin: 0 0 10px 0;
  display: inline-block;
  text-decoration: none;
  padding: 0;  /* Set padding to 0 to ensure the size is exactly 44x44 */
  box-sizing: border-box;  /* Ensure padding is included in the size */
  width: 44px;  /* Explicitly set width */
  height: 44px;  /* Explicitly set height */
  line-height: 44px;  /* Center text vertically */
  text-align: center;  /* Center text horizontally */
  color: #db0000;  /* Set text color */
}



.restaurant-item__content h3 a {
  display: inline-block;
  color: #db0000;
  text-decoration: none;
  padding: 10px 15px;
  box-sizing: border-box;  /* Ensure padding is included in the size */
  width: 44px;  /* Explicitly set width */
  height: 44px;  /* Explicitly set height */
  line-height: 44px;  /* Center text vertically if needed */
  text-align: center;  /* Center text horizontally */
}


.restaurant-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

/* Restaurant Detail */

.restaurant {
  grid-template-columns: 1fr; /* Change to single column */
  display: grid;
  gap: 18px 16px;
  max-width: 800px;
  margin: 0 auto;
}

.restaurant .restaurant__title,
.restaurant .restaurant__overview {
  grid-column: 1 / -1; /* Span the single column */
}

.restaurant .restaurant__poster {
  width: 100%;
  max-width: 400px;
}

.restaurant .restaurant__info h4 {
  margin: 8px 0;
}

.restaurant__container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.restaurant__poster {
  width: 100%;
  max-width: 200px;
  height: auto;
  margin-right: 20px;
  margin-bottom: 20px;
}

.restaurant__info {
  flex: 1;
  width: 100%;
}

.restaurant__name {
  margin-top: 0;
  margin-bottom: 10px;
}

.restaurant__description {
  margin-bottom: 20px;
}

.restaurant__menu {
  margin-bottom: 20px;
}

.restaurant__menu ul {
  list-style-type: none;
  padding: 0;
}

.restaurant__menu li {
  margin-bottom: 5px;
}

/* Review Section */

.review {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 10px;
}

.review p {
  margin: 5px 0;
}

.restaurant__reviews {
  margin-top: 20px;
}

/* Like Button */

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* Add Review Form */

.add-review-form {
  margin-top: 20px;
}

.add-review-form h3 {
  margin-bottom: 10px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input[type="text"],
.form-group textarea {
  width: 100%;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px 15px;
  box-sizing: border-box;  /* Ensure padding is included in the size */
  line-height: 44px;  /* Center text vertically if needed */
}

button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
  box-sizing: border-box;  /* Ensure padding is included in the size */
  line-height: 44px;  /* Center text vertically if needed */
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

/* Slideshow */

.slides{
  width: 100%;
  position: relative;
}

.slides .slide{
  display: none;
}

.slides .slide img{
  width: 100%;
  animation-name: fade;
  animation-duration: 1.5s;
}

.slides .navigation{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display: flex;
  justify-content: space-between;
  width: 100%;
  min-width: 44px;
  min-height: 44px;
}

.slides .navigation .prev, .slides .navigation .next{
  cursor: pointer;
  padding: 16px;
  font-weight: bold;
  font-size: 20px;
  color: white;
  background: rgba(0,0,0,0.2);
  user-select: none;
  transition: 0.6s ease;
  min-width: 44px;
  min-height: 44px;
}

.slides .navigation .prev:hover, .slides .navigation .next:hover{
  background: rgba(0,0,0,1);
}

/* Search Container */

.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

#search-input {
  box-sizing: border-box;
  width: calc(100% - 44px); /* Menyesuaikan lebar input agar totalnya sesuai */
  height: 44px; /* Mengatur tinggi menjadi 44px */
  padding: 10px; /* Padding tetap */
  border: 1px solid #ccc; /* Border tetap */
  border-radius: 4px 0 0 4px;
  font-size: 14px;
  outline: none;
  transition: border 0.3s;
}

#search-input:focus {
  border-color: #00aaff;
}

#search-button {
  box-sizing: border-box;
  width: 44px; /* Mengatur lebar menjadi 44px */
  height: 44px; /* Mengatur tinggi menjadi 44px */
  padding: 0; /* Menghapus padding */
  border: none; /* Menghapus border */
  background-color: #00aaff;
  color: white;
  font-size: 14px;
  cursor: pointer;
  border-radius: 0 4px 4px 0;
  transition: background-color 0.3s;
}

#search-button:hover {
  background-color: #0088cc;
}


/* Footer */

footer {
  padding: 16px;
}

footer p {
  text-align: center;
  color: #aaaaaa;
}

footer p a {
  color: #db0000;
  text-decoration: none;
}
