uran tinggi tetap 44px */\n  display: flex;  /* Menggunakan flexbox untuk penempatan konten */\n  align-items: center;  /* Konten berada di tengah secara vertikal */\n  justify-content: center;  /* Konten berada di tengah secara horizontal */\n  border-radius: 4px;  /* Menambahkan border radius untuk gaya */\n  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);  /* Menambahkan shadow untuk visibilitas */\n  text-align: center;\n  font-size: 10px;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\nmain {\n  padding: 32px;\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;  /* Add this line */\n}\n\n.content {\n  margin: 0 auto;\n  min-height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;  /* Add this line */\n}\n\n.content .content__heading {\n  font-weight: normal;\n  text-align: center;  /* Add this line if needed */\n}\n\n/* Home Page Specific */\n.home-page-header {\n  text-align: center;\n  margin-bottom: 20px;\n}\n\n/* Restaurants */\n\n.restaurants {\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 16px;\n  margin: 32px 0;\n}\n\n/* Restaurant Item */\n\n.restaurant-item {\n  width: 100%;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  border-radius: 8px;\n  overflow: hidden;\n}\n\n.restaurant-item__header {\n  position: relative;\n}\n\n.restaurant-item__header .restaurant-item__header__poster {\n  width: 100%;\n}\n\n.restaurant-item__header .restaurant-item__header__rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  left: 0;\n  display: inline-block;\n  background-color: black;\n  color: white;\n}\n\n.restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\n  margin-left: 10px;\n}\n\n.restaurant-item__content {\n  padding: 16px;\n}\n\n.restaurant-item__content h3 {\n  margin: 0 0 10px 0;\n  display: inline-block;\n  text-decoration: none;\n  padding: 0;  /* Set padding to 0 to ensure the size is exactly 44x44 */\n  box-sizing: border-box;  /* Ensure padding is included in the size */\n  width: 44px;  /* Explicitly set width */\n  height: 44px;  /* Explicitly set height */\n  line-height: 44px;  /* Center text vertically */\n  text-align: center;  /* Center text horizontally */\n  color: #db0000;  /* Set text color */\n}\n\n\n\n.restaurant-item__content h3 a {\n  display: inline-block;\n  color: #db0000;\n  text-decoration: none;\n  padding: 10px 15px;\n  box-sizing: border-box;  /* Ensure padding is included in the size */\n  width: 44px;  /* Explicitly set width */\n  height: 44px;  /* Explicitly set height */\n  line-height: 44px;  /* Center text vertically if needed */\n  text-align: center;  /* Center text horizontally */\n}\n\n\n.restaurant-item__content p {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4; /* number of lines to show */\n  -webkit-box-orient: vertical;\n}\n\n/* Restaurant Detail */\n\n.restaurant {\n  grid-template-columns: 1fr; /* Change to single column */\n  display: grid;\n  gap: 18px 16px;\n  max-width: 800px;\n  margin: 0 auto;\n}\n\n.restaurant .restaurant__title,\n.restaurant .restaurant__overview {\n  grid-column: 1 / -1; /* Span the single column */\n}\n\n.restaurant .restaurant__poster {\n  width: 100%;\n  max-width: 400px;\n}\n\n.restaurant .restaurant__info h4 {\n  margin: 8px 0;\n}\n\n.restaurant__container {\n  display: flex;\n  flex-wrap: wrap;\n  margin-bottom: 20px;\n}\n\n.restaurant__poster {\n  width: 100%;\n  max-width: 200px;\n  height: auto;\n  margin-right: 20px;\n  margin-bottom: 20px;\n}\n\n.restaurant__info {\n  flex: 1;\n  width: 100%;\n}\n\n.restaurant__name {\n  margin-top: 0;\n  margin-bottom: 10px;\n}\n\n.restaurant__description {\n  margin-bottom: 20px;\n}\n\n.restaurant__menu {\n  margin-bottom: 20px;\n}\n\n.restaurant__menu ul {\n  list-style-type: none;\n  padding: 0;\n}\n\n.restaurant__menu li {\n  margin-bottom: 5px;\n}\n\n/* Review Section */\n\n.review {\n  background-color: #f9f9f9;\n  border-radius: 8px;\n  padding: 10px;\n  margin-bottom: 10px;\n}\n\n.review p {\n  margin: 5px 0;\n}\n\n.restaurant__reviews {\n  margin-top: 20px;\n}\n\n/* Like Button */\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/* Add Review Form */\n\n.add-review-form {\n  margin-top: 20px;\n}\n\n.add-review-form h3 {\n  margin-bottom: 10px;\n}\n\n.form-group {\n  margin-bottom: 15px;\n}\n\n.form-group label {\n  display: block;\n  margin-bottom: 5px;\n}\n\n.form-group input[type=\"text\"],\n.form-group textarea {\n  width: 100%;\n  padding: 8px;\n  font-size: 14px;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n  padding: 10px 15px;\n  box-sizing: border-box;  /* Ensure padding is included in the size */\n  line-height: 44px;  /* Center text vertically if needed */\n}\n\nbutton[type=\"submit\"] {\n  background-color: #007bff;\n  color: #fff;\n  border: none;\n  border-radius: 4px;\n  padding: 10px 15px;\n  box-sizing: border-box;  /* Ensure padding is included in the size */\n  line-height: 44px;  /* Center text vertically if needed */\n  cursor: pointer;\n}\n\nbutton[type=\"submit\"]:hover {\n  background-color: #0056b3;\n}\n\n/* Slideshow */\n\n.slides{\n  width: 100%;\n  position: relative;\n}\n\n.slides .slide{\n  display: none;\n}\n\n.slides .slide img{\n  width: 100%;\n  animation-name: fade;\n  animation-duration: 1.5s;\n}\n\n.slides .navigation{\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%,-50%);\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.slides .navigation .prev, .slides .navigation .next{\n  cursor: pointer;\n  padding: 16px;\n  font-weight: bold;\n  font-size: 20px;\n  color: white;\n  background: rgba(0,0,0,0.2);\n  user-select: none;\n  transition: 0.6s ease;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.slides .navigation .prev:hover, .slides .navigation .next:hover{\n  background: rgba(0,0,0,1);\n}\n\n/* Search Container */\n\n.search-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  margin: 20px 0;\n}\n\n#search-input {\n  box-sizing: border-box;\n  width: calc(100% - 44px); /* Menyesuaikan lebar input agar totalnya sesuai */\n  height: 44px; /* Mengatur tinggi menjadi 44px */\n  padding: 10px; /* Padding tetap */\n  border: 1px solid #ccc; /* Border tetap */\n  border-radius: 4px 0 0 4px;\n  font-size: 14px;\n  outline: none;\n  transition: border 0.3s;\n}\n\n#search-input:focus {\n  border-color: #00aaff;\n}\n\n#search-button {\n  box-sizing: border-box;\n  width: 44px; /* Mengatur lebar menjadi 44px */\n  height: 44px; /* Mengatur tinggi menjadi 44px */\n  padding: 0; /* Menghapus padding */\n  border: none; /* Menghapus border */\n  background-color: #00aaff;\n  color: white;\n  font-size: 14px;\n  cursor: pointer;\n  border-radius: 0 4px 4px 0;\n  transition: background-color 0.3s;\n}\n\n#search-button:hover {\n  background-color: #0088cc;\n}\n\n\n/* Footer */\n\nfooter {\n  padding: 16px;\n}\n\nfooter p {\n  text-align: center;\n  color: #aaaaaa;\n}\n\nfooter p a {\n  color: #db0000;\n  text-decoration: none;\n}\n"],"sourceRoot":""}]);
